import {t} from 'sn-translate';

export const renderPages = props => {
	const {pageItems} = props;
	return (
		<div className="sn-pagination-pages">
			{pageItems.map(page => getPage(page, props))}
		</div>
	);
};

const getPage = (page, {isSelected, onClick}, isAllButOne = false) => {
	const isActive = isSelected(page);
	const ariaLabel = t('Page {0}', page);
	return (
		<a
			key={page.toString()}
			className={`sn-pagination-btn ${getClass(isActive, isAllButOne)}`}
			on-click={() => onClick(page)}
			href="javascript:void(0)"
			aria-current={isActive}
			title={ariaLabel}
			aria-label={ariaLabel}>
			{page}
		</a>
	);
};

const getClass = (isActive, isAllButOne) =>
	isAllButOne ? 'is-disabled' : isActive ? 'is-selected' : '';
