import {createCustomElement} from '@servicenow/ui-core';
import snabbdom, {Fragment} from '@servicenow/ui-renderer-snabbdom';
import styles from '../_nowPagination.scss';
import {SET_PAGE} from '../constants';
import {renderPages} from './pages';
import {t} from 'sn-translate';
import '@servicenow/now-icon';

const getIsDisabledClass = isDisabled => (isDisabled ? 'is-disabled' : '');

const getDisabledValues = model => ({
	first: model.previousDisabled,
	previous: model.previousDisabled,
	next: model.nextDisabled,
	last: model.nextDisabled
});

const view = (state, dispatch) => {
	const {
		properties: {
			hidePages,
			hideFirstPage,
			hidePreviousPage,
			hideNextPage,
			hideLastPage,
			paginationModel,
			isSelected
		}
	} = state;

	const {page, maxPage, pageItems} = paginationModel;

	const disabled = getDisabledValues(paginationModel);
	const firstPageClassNames = `sn-pagination-btn ${getIsDisabledClass(
		disabled.first
	)}`;
	const previousPageClassNames = `sn-pagination-btn ${getIsDisabledClass(
		disabled.previous
	)}`;
	const nextPageClassNames = `sn-pagination-btn ${getIsDisabledClass(
		disabled.next
	)}`;
	const lastPageClassNames = `sn-pagination-btn ${getIsDisabledClass(
		disabled.last
	)}`;
	const pageIncrementValue = page + 1 <= maxPage ? page + 1 : maxPage;

	const onChange = page => dispatch(SET_PAGE, {page}, {paginationModel});

	const pagesProps = {
		isSelected,
		pageItems,
		onClick: onChange
	};

	return (
		<Fragment>
			{!hideFirstPage ? (
				<button
					type="button"
					className={firstPageClassNames}
					role="link"
					title={t('First Page')}
					aria-label={t('First Page')}
					disabled={disabled.first}
					aria-disabled={`${disabled.first}`}
					on-click={() => onChange(1)}>
					<now-icon icon="arrow-left-most-outline" size="sm" />
				</button>
			) : null}
			{!hidePreviousPage ? (
				<button
					type="button"
					className={previousPageClassNames}
					role="link"
					title={t('Previous Page')}
					aria-label={t('Previous Page')}
					disabled={disabled.previous}
					aria-disabled={`${disabled.previous}`}
					on-click={() => onChange(page - 1)}>
					<now-icon icon="arrow-left-outline" size="sm" />
				</button>
			) : null}
			{!hidePages ? renderPages(pagesProps) : null}
			{!hideNextPage ? (
				<button
					type="button"
					className={nextPageClassNames}
					title={t('Next Page')}
					aria-label={t('Next Page')}
					role="link"
					disabled={disabled.next}
					aria-disabled={`${disabled.next}`}
					on-click={() => onChange(pageIncrementValue)}>
					<now-icon icon="arrow-right-outline" size="sm" />
				</button>
			) : null}
			{!hideLastPage ? (
				<button
					type="button"
					className={lastPageClassNames}
					title={t('Last Page')}
					aria-label={t('Last Page')}
					role="link"
					disabled={disabled.last}
					aria-disabled={`${disabled.last}`}
					on-click={() => onChange(maxPage)}>
					<now-icon icon="arrow-right-most-outline" size="sm" />
				</button>
			) : null}
		</Fragment>
	);
};

createCustomElement('sn-pagination-controls', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		hidePages: {
			default: false
		},
		hideFirstPage: {
			default: false
		},
		hidePreviousPage: {
			default: false
		},
		hideNextPage: {
			default: false
		},
		hideLastPage: {
			default: false
		},
		paginationModel: {
			default: {}
		},
		isSelected: {
			default: false
		}
	},
	styles
});
