import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import isEqual from 'lodash/isEqual';
import get from 'lodash/get';
import {default as getModel} from './model';
import styles from './_nowPagination.scss';
import {renderRange} from './subcomponents/range';
import './subcomponents/controls';
import './subcomponents/limitSelector';
import {
	ITEM_CLICKED,
	PAGE_UPDATE,
	SET_PAGE,
	SET_LIMIT,
	LIMIT_UPDATE,
	CLICK_LIMIT,
	OPENED_SET
} from './constants';
import {t} from 'sn-translate';
import packageJson from '../package.json';

const PAGINATION_LABEL = t('Pagination');

const renderPagination = (
	controlProps,
	isSelected = page =>
		isEqual(page, get(controlProps, 'paginationModel.page', 1))
) => {
	return <sn-pagination-controls {...controlProps} isSelected={isSelected} />;
};

const view = state => {
	const {
		paginationModel,
		properties: {
			hideRange,
			hideRowCount,
			hideFirstPage,
			hidePreviousPage,
			hidePages,
			hideNextPage,
			hideLastPage,
			hideLimitSelector,
			limitLabel,
			rangeLabel
		}
	} = state;

	const {
		hiddenCount,
		recordCount,
		rangeStart,
		rangeEnd,
		limit
	} = paginationModel;

	const controlProps = {
		hidePages,
		hideFirstPage,
		hidePreviousPage,
		hideNextPage,
		hideLastPage,
		paginationModel
	};
	const hasHiddenRecords = hiddenCount > 0;
	const singularHiddenMessage = t(
		'{0} row removed from this list by security constraints.',
		hiddenCount
	);
	const pluralHiddenMessage = t(
		'{0} rows removed from this list by security constraints.',
		hiddenCount
	);
	const hiddenMessage =
		hiddenCount === 1 ? singularHiddenMessage : pluralHiddenMessage;

	const defaultContainerClass = 'sn-pagination-content';
	const lastContainerClass = !hideLimitSelector
		? `${defaultContainerClass} has-controls`
		: defaultContainerClass;

	return (
		<div
			className="sn-pagination"
			role="navigation"
			title={PAGINATION_LABEL}
			aria-label={PAGINATION_LABEL}>
			{hasHiddenRecords ? (
				<div className="sn-pagination-container -security">
					<span role="status">{hiddenMessage}</span>
				</div>
			) : null}
			<div className="sn-pagination-container">
				<div className={defaultContainerClass}>
					{renderRange({
						hideRange,
						hideRowCount,
						rangeEnd,
						rangeLabel,
						rangeStart,
						recordCount
					})}
				</div>
				<div className={defaultContainerClass}>
					{renderPagination(controlProps)}
				</div>
				<div className={lastContainerClass}>
					{!hideLimitSelector ? (
						<sn-pagination-limit-selector limit={limit} label={limitLabel} />
					) : null}
				</div>
			</div>
		</div>
	);
};

const setPageHandler = ({
	dispatch,
	action: {
		payload: {page},
		meta: {paginationModel}
	}
}) => {
	if (
		page < 1 ||
		page > paginationModel.maxPage ||
		isEqual(page, paginationModel.page)
	)
		return;

	dispatch(PAGE_UPDATE, {page});
};

const setLimitHandler = ({dispatch, action}) => {
	dispatch(LIMIT_UPDATE, {limit: get(action, 'payload.value[0]')});
};

const transformState = state => {
	const {
		properties: {
			limit,
			limitOverride,
			page,
			recordCount,
			visibleCount,
			maxPages
		}
	} = state;

	return {
		...state,
		paginationModel: getModel(
			{limit, limitOverride, page, recordCount, visibleCount},
			maxPages
		)
	};
};

/**
 * A component that displays pagination controls, limit selector, and records displayed for use in navigating
 * through paged data.
 *
 * ```
 * <now-pagination {...paginationProps} />
 * ```
 *
 * @seismicElement now-pagination
 * @summary A component that displays pagination controls, limit selector, and records displayed for use in navigating
 * through paged data.
 */
createCustomElement('now-pagination', {
	renderer: {
		type: snabbdom,
		view,
		transformState
	},
	properties: {
		/**
		 * how many records to render per page. Default: 20.
		 * @type {number}
		 */
		limit: {
			default: 20
		},
		/**
		 * initial page to render
		 * @type {number}
		 */
		page: {
			default: 1
		},
		/**
		 * The total records in the collection.
		 * @type {number}
		 */
		recordCount: {},
		/**
		 * The *visible* records in the collection (ACLs, business rules, etc)
		 * @type {number}
		 */
		visibleCount: {},
		/**
		 * Gives the ability to remove the pagination range text from the component.
		 * @type {boolean}
		 */
		hideRange: {
			default: false
		},
		/**
		 * Gives the ability to remove the pagination row count text from the component.
		 * @type {boolean}
		 */
		hideRowCount: {
			default: false
		},
		/**
		 * Gives the ability to remove the 'jump-to-first-page' button from the component.
		 * @type {boolean}
		 */
		hideFirstPage: {
			default: false
		},
		/**
		 * Gives the ability to remove the 'previous-page' button from the component.
		 * @type {boolean}
		 */
		hidePreviousPage: {
			default: false
		},
		/**
		 * Gives the ability to remove the page buttons (i.e. As in Page: 1, 2, 3, 4, etc.) from the component.
		 * @type {boolean}
		 */
		hidePages: {
			default: false
		},
		/**
		 * Gives the ability to remove the 'next-page' button from the component.
		 * @type {boolean}
		 */
		hideNextPage: {
			default: false
		},
		/**
		 * Gives the ability to remove the 'jump-to-last-page' button from the component.
		 * @type {boolean}
		 */
		hideLastPage: {
			default: false
		},
		/**
		 * Gives the ability to remove the limit selector from the component.
		 * @type {boolean}
		 */
		hideLimitSelector: {
			default: false
		},
		/**
		 * Determines the number of page buttons to show.
		 * @type {number}
		 */
		maxPages: {
			default: 10
		},
		/**
		 * Determines the post-count text (label) of the limit selector (i.e. 'Display {0} <insert-post-count-text-here>').
		 * @type {string}
		 */
		limitLabel: {
			default: 'rows per page'
		},
		/**
		 * Overrides the pagination range text (i.e. Showing 1-20 records) with custom message.
		 * @type {string}
		 */
		rangeLabel: {
			default: undefined
		},
		limitOverride: {}
	},
	styles,
	actionHandlers: {
		[SET_PAGE]: {effect: setPageHandler, private: true},
		[SET_LIMIT]: {effect: setLimitHandler, private: true},
		[CLICK_LIMIT]: {
			private: true
		},
		[OPENED_SET]: {
			private: true
		},
		[ITEM_CLICKED]: {
			stopPropagation: true
		}
	},
	dispatches: {
		/**
		 * Dispatched when the user clicks the pagination controls (i.e. first page, next page, or a specific page).
		 * Payload will include the page the user wants to go to.
		 * @type {{page: number}}
		 */
		[PAGE_UPDATE]: {},
		/**
		 * Dispatched when the user selects a new limit (i.e. the amount of records to display on a given page).
		 * Payload will include the limit amount (i.e. 10, 20, 50, 100).
		 * @type {{limit: number}}
		 */
		[LIMIT_UPDATE]: {}
	},
	onConnect(host) {
		if (!window.listComponentRegistry) {
			window.listComponentRegistry = new WeakMap();
		}
		window.listComponentRegistry.set(host, packageJson.version);
	},
	onDisconnect(host) {
		if (window.listComponentRegistry) {
			window.listComponentRegistry.delete(host);
		}
	}
});
