import {t} from 'sn-translate';

export const renderRange = props => {
	const {
		recordCount,
		rangeStart,
		rangeEnd,
		hideRange,
		hideRowCount,
		rangeLabel
	} = props;
	const hasRecords = recordCount !== 0;
	const recordsMessage = t(
		'Showing {0}-{1} of {2}',
		rangeStart,
		rangeEnd,
		recordCount
	);
	const noCountMessage = t('Showing {0}-{1}', rangeStart, rangeEnd);
	const noRangeMessage = t('{0} records', recordCount);

	let message = hasRecords ? recordsMessage : noRangeMessage;

	if (hideRange && hideRowCount) return null;

	if (hideRowCount && !hasRecords) return null;
	else if (hideRowCount) message = noCountMessage;

	if (hideRange) message = noRangeMessage;

	return <span>{rangeLabel ? rangeLabel : message}</span>;
};
