import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from '../_nowPagination.scss';
import {t} from 'sn-translate';
import {LIMITS} from '../constants';
import '@servicenow/now-dropdown';

const hasLimit = value => {
	value = parseInt(value, 10);
	return LIMITS.filter(limit => value === limit).length !== 0;
};

const getLimitChoices = limit =>
	hasLimit(limit) ? LIMITS : LIMITS.concat([limit]);

const view = state => {
	const {
		properties: {limit},
		nowChoices,
		limitAriaLabel,
		rowLabel
	} = state;

	return (
		<div className="sn-pagination-control">
			<now-dropdown
				config-aria={{'aria-label': limitAriaLabel}}
				panel-fit-props={['top-start bottom-start']}
				items={nowChoices}
				select="single"
				selected-items={[limit.toString()]}
				show-padding
				size="md"
				variant="secondary"
				manage-selected-items
				bare
			/>
			<span className="sn-pagination-control-label" aria-label={rowLabel}>
				{rowLabel}
			</span>
		</div>
	);
};

createCustomElement('sn-pagination-limit-selector', {
	renderer: {
		type: snabbdom,
		view,
		transformState(state) {
			const {
				properties: {limit, label}
			} = state;
			const choices = getLimitChoices(limit);
			const nowChoices = choices.map(choice => ({
				id: choice.toString(),
				label: choice.toString()
			}));
			const limitAriaLabel = t(`Display {0} ${label}`, limit);
			const rowLabel = t(`${label}`);
			return {
				...state,
				nowChoices,
				limitAriaLabel,
				rowLabel
			};
		}
	},
	properties: {
		limit: {
			default: 20
		},
		label: {
			default: 'rows per page'
		},
		select: {}
	},
	styles
});
